.title1 {
  margin: 0;
  font-weight: 600;
  font-size: 5rem;
  line-height: 124%;
  color: var(--chernyy-100);

  @include small-tablet {
    font-size: 4rem;
  }
  @include mobile {
    font-size: 3.4rem;
  }
}
.title2 {
