  }
}

.desc1 {
  margin: 0;
  font-weight: 600;
  font-size: 1.8rem;
  line-height: 133%;
  color: var(--chernyy-100);

  &--light {
    color: var(--belyy);
  }

  &--500 {
    font-weight: 500;
  }
  &--400 {
    font-weight: 400;
  }

  span {
    color: var(--siniy-100);
  }
  p {
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }
  b {
    font-weight: 600;
  }
  ul,
  ol {
    list-style: none;
    margin: 0;
    padding: 0;
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
    li {
      &::before {
        content: "";
        display: inline-block;
        margin-right: 1rem;
        width: 1px;
        height: 2rem;
        background: var(--zelenyy-100);
        transform: rotate(20deg);
      }

      &:not(:last-child) {
        margin-bottom: 0.8rem;
      }
    }
  }
}

